// App.tsx - 메인 애플리케이션 컴포넌트
import React, { useState, useEffect } from 'react';
import LoadingScreen from './pages/Sloading/LoadingScreen.tsx';
import Dashboard from './pages/Dashboard/Dashboard.tsx';

const App: React.FC = () => {
  const [isLoading, setIsLoading] = useState(true);

  // 로딩 완료 핸들러
  const handleLoadingComplete = (redirectPath: string) => {
    setIsLoading(false);
  };

  // 새로고침 감지 및 로딩 화면 재표시
  useEffect(() => {
    const handleBeforeUnload = () => {
      setIsLoading(true);
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, []);

  // 첫 방문 여부 확인 (세션 기반)
  useEffect(() => {
    const hasVisited = sessionStorage.getItem('aws_iot_visited');
    
    // 방문 표시
    sessionStorage.setItem('aws_iot_visited', 'true');
  }, []);

  return (
    <>
      {isLoading ? (
        <LoadingScreen onLoadingComplete={handleLoadingComplete} />
      ) : (
        <Dashboard />
      )}
    </>
  );
};

export default App;