/**
 * 웹 성능 측정 및 백엔드 모니터링 연계 유틸리티
 * 
 * 백엔드 협업 포인트:
 * - 성능 메트릭을 백엔드 모니터링 시스템으로 전송 가능
 * - 사용자 경험 데이터를 백엔드에서 분석 및 대시보드 표시
 * - Core Web Vitals 데이터를 통한 프론트엔드 성능 최적화 지표 제공
 * 
 * 측정 항목:
 * - CLS: Cumulative Layout Shift (레이아웃 이동)
 * - FID: First Input Delay (첫 입력 지연)
 * - FCP: First Contentful Paint (첫 콘텐츠 렌더링)
 * - LCP: Largest Contentful Paint (최대 콘텐츠 렌더링)
 * - TTFB: Time to First Byte (첫 바이트 수신 시간)
 */

// Web Vitals 메트릭 타입 정의
type ReportHandler = (metric: {
  name: string;
  value: number;
  id: string;
  delta: number;
}) => void;

/**
 * 성능 메트릭 수집 및 처리 함수
 * 
 * 백엔드 연계 확장 가능:
 * - 성능 데이터를 API를 통해 백엔드로 전송
 * - 사용자별 성능 프로파일링 데이터 수집
 * - 실시간 성능 모니터링 및 알림 시스템 구축
 * 
 * @param onPerfEntry 성능 메트릭 처리 콜백 함수
 */
const reportWebVitals = (onPerfEntry?: ReportHandler): void => {
  if (onPerfEntry && typeof onPerfEntry === 'function') {
    import('web-vitals').then(({ getCLS, getFID, getFCP, getLCP, getTTFB }) => {
      getCLS(onPerfEntry);  // 레이아웃 안정성 측정
      getFID(onPerfEntry);  // 사용자 상호작용 반응성 측정
      getFCP(onPerfEntry);  // 첫 콘텐츠 표시 속도 측정
      getLCP(onPerfEntry);  // 페이지 로딩 완료 속도 측정
      getTTFB(onPerfEntry); // 서버 응답 속도 측정 (백엔드 성능 지표)
    });
  }
};

export default reportWebVitals;
